import { graphql } from "gatsby"
import { Node, PostFrontmatter } from "src/utils/types"
import { React } from "src/utils/view"

import FullSlide from "src/components/post/slide"

export default function Post({ data }: { data: any }) {
  const markdownRemark: Node<PostFrontmatter> = data.markdownRemark
  const { frontmatter, internal } = markdownRemark
  return <FullSlide frontmatter={frontmatter} html={internal.content} />
}

export const pageQuery = graphql`
  query($path: String!) {
    markdownRemark(frontmatter: { url: { eq: $path } }) {
      internal {
        content
      }
      frontmatter {
        date(formatString: "MMMM DD, YYYY")
        url
        title
        author
        categories
        tags
        type
      }
    }
  }
`
